/* 
	-----------------------------------------------
		HEADER LOOGKPAGE
	-----------------------------------------------
*/

.header-lookpage {
  height: 100vh;
	background-color: $light-grey;
}

.header-lookpage__container {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-content: center;
}



// ###  Большая вешалка  ###
//---------------------------------------
.header__bg-pic {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 0;
	transform: translate(-50%, -50%);
	width: 95%;
	height: 70vh;
	background-image: url(../img/svg/logo_2.svg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}


// ###  Лого  ###
//---------------------------------------
.header-logo {
	position: absolute;
	top: 19%;
	left: 50%;
	transform: translateX(-50%);
	text-align: center;
	z-index: 5;
}

.header-logo__icon {
	svg {
	  width: 109px;
	  height: 75px;
	}
}



// ###  Заголовок / Описиние  ###
//---------------------------------------
.header__title-and-desrc-wrap {
	position: absolute;
	top: 42%;
	left: 50%;
	transform: translateX(-50%);
}


.header__title {
	margin-bottom: 10px;
  font-family: $demi;
  font-size: rem(48);
  text-align: center;

  position: relative;
	z-index: 5;
}

.header-descr {
	max-width: 520px;
	width: 100%;
	margin: 0 auto 22px;
  font-size: rem(21);
  line-height: 1.43;
  text-align: center;
  position: relative;
	z-index: 5;
}


.header-desrc__line {
	display: block;
}


// ###  Кнопка / Кнопка Скролла  ###
//---------------------------------------
.header__btn-wrap {
	display: flex;
	justify-content: center;
	position: relative;
	z-index: 5;
}


.header__btn-mobile {
	display: none;
}

//---------------------------------------
.header__btn-scroll-wrap {
	width: 100%;
	position: absolute;
	bottom: 15px;
	left: 0;
	z-index: 5;
	text-align: center;

}

.btn-scroll-wrap {
	display: inline-block;
	cursor: pointer;
	&:hover {
		.btn-scroll__text {
			opacity: 0.5;
		}

		.btn-scroll {
			opacity: 0.5;
		}
	}
}

.btn-scroll__text {
	margin-bottom: 3px;
  font-size: rem(12);
  line-height: 2;
  letter-spacing: 3px;
  text-align: center;
  color: rgba($black, 0.2);
  text-transform: uppercase;
  transition: opacity 0.5s;
}

.btn-scroll__icon-wrap {
	height: 32px;
	transition: opacity 0.5s;
}

.btn-scroll{
	display: inline-block;
	position: relative;
	top: 5px;
	animation: scroll 2s infinite;
	text-decoration: none;
	font-size: 0;
	transition: opacity 0.5s;

	svg {
	  width: 20px;
	  height: 32px;
	}
}


@keyframes scroll {
  0% {top: 5px;}
  50% { top: 0px;}
  100% {top: 5px;}
}


//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {


	.header-lookpage__container {
		justify-content: space-between;

		padding-top: 12%;
		padding-bottom: 25%;
	}


	.header-lookpage {
		// что бы блок не прыгал на мобильном
		// при height: 100vh
		transition-duration: 999999s;
	}

	.header__title-and-desrc-wrap {
		width: 100%;
		padding-left: 10px;
		padding-right: 10px;

		position: relative;
		top: -4%;
		left: auto;
		bottom: auto;
		transform: translateX(0%);
		margin-top: auto;
	}


	//---------------------------------------
	.header__bg-pic,
	.header__btn-scroll-wrap,
	.header__btn-wrap {
		display: none;
	}

	//---------------------------------------
	.header-logo {
		position: relative;
		top: auto;
		left: auto;
		transform: translateX(0%);
	}

	.header-logo__icon {
		svg {
		  width: 110px;
  		height: 75px;
		}
	}

	//---------------------------------------
	.header__title {
	  font-size: rem(24);
	}

	.header-descr {
		width: 280px;
		margin-bottom: 0px;
	  font-size: rem(18);
	  line-height: 1.5;
	}
	
	.header-desrc__line {
		display: inline-block;
	}

	.header-desrc__line {
		display: block;
	}

	
	//---------------------------------------
	.header__btn-mobile {
		display: block;
		padding-left: 10px;
		padding-right: 10px;
		margin-top: auto;
	}

}

