  text-align: center;
  text-decoration: none;
  transition: all 200ms ease-in;
  font-family: "Futura Demi", sans-serif; }
  .btn:hover {
    background-color: #ffcdcd; }
    .btn:hover .btn__text {
      color: #262222; }
    .btn:hover .icon svg {
      fill: #262222; }
  .btn.btn-share {
    width: 300px;
    justify-content: center;
    align-items: center; }
    .btn.btn-share .btn__text {
      margin: 0; }
  .btn .icon {
    display: block;
    margin-right: 10px;
    position: relative;
    top: 2px; }
    .btn .icon svg {
      width: 16px;
      height: 12px;
      fill: #fff;
      transition: all 200ms ease-in; }
  .btn .icon-share {
    display: block; }
    .btn .icon-share svg {
      position: relative;
      top: -4px; }

.btn__text {
  margin: auto;
  font-size: 0.8125rem;
  line-height: 2.3;
  letter-spacing: 4.1px;
  text-align: center;
  color: #ffffff;
