/* 
	-----------------------------------------------
		LOOKPAGE ITEM
	-----------------------------------------------
*/

.lookpage-item {
	min-height: 670px;
	height: 87vh;
	position: relative;
	display: flex;
}


// ###  Заголовок для мобильного  ###
//---------------------------------------
.lookpage-item__mobile-title {
	display: none;
	max-width: 124px;
	width: 100%;
	margin: 0 auto;
	position: relative;
	left: -20px;
	padding-top: 36px;
	padding-bottom: 52px;
}

.mobile-title__circle {
	width: 121px;
  height: 121px;
	border-radius: 50%;
	position: absolute;
	top: 54%;
	left: calc(65% - 1px);
	transform: translate(-50%, -50%);
	z-index: -1;
}

.mobile-title__digit-wrap {
	margin-right: -20px;
	margin-bottom: 3px;
  font-family: $demi;
  font-size: rem(30);
}

.mobile-title__sign {
	display: inline-block;
}

.mobile-title__digit {
	position: relative;
	top: 1px;
}


.mobile-title__line {
	width: 6px;
  height: 86px;
  transform: rotate(-50deg);
  background-color: $black;

  position: absolute;
  top: -7px;
  right: calc(14% - 4px);
}

.mobile-title__title {
  font-family: $demi;
  font-size: rem(30);
  line-height: 0.9;
}



// ###  Изображение  ###
//---------------------------------------
.lookpage-item__item-pic {
	width: calc(100% - 424px);
	background-color: #fff;
	position: relative;

	img {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		margin: auto;
		max-width: 80%;
		max-height: 80%;
		height: auto;
		width: auto;
	}

	&.item-pic--right {
		order: 1;
	}
}



// ###  Информация  ###
//---------------------------------------
.lookpage-item__item-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	position: relative;
	width: 424px;
	padding-left: 40px;
	background-color: $light-grey;
	color: $black;

	&.item-infom--border-top {
		border-top: 1px solid rgba(38,34,34,0.05);	
	}
}


//---------------------------------------
.item-info__icon {
	position: absolute;
	top: 12%;
	left: 50%;
	transform: translateX(-50%);

	.icon {
		display: inline-block;	

		svg {
		  width: 79px;
		  height: 42px;
		}
	}
}

//---------------------------------------
.item-info__middle-block {
	margin-top: 40px;

	&.middle-block--share {
		margin-top: 23px;
	}
}

.item-info__item-number-wrap {
	margin-bottom: 9px;
}

.item-number__sign {
	display: inline-block;
  font-family: $demi;
  font-size: rem(24);
}

.item-number__digit {
	display: inline-block;
	height: 34px;
	font-family: $slon;
  font-size: rem(48);
}

//---------------------------------------
.item-info__title {
  margin-bottom: 4px;
  font-size: rem(24);
  font-family: $demi;

  &.item-info__title--share {
  	margin-bottom: 3px;
  }
}

//---------------------------------------
.item-info__clothes-list {
	margin: 0;
	padding: 0;
	list-style: none;

	margin-bottom: 12px;

	&.clothes-list--share {
		margin-bottom: 14px;
	}
}

.clothes-item {
	display: flex;
	align-items: center;
	line-height: 38px;
	line-height: 2;
}

.clothes-item__name {
	text-decoration: none;
  font-size: rem(18);
	position: relative;
	color: $black;
	transition: all 0.5ms ease-in;

	&:hover {
		&:after {
			opacity: 0;
		}
	}

  &:after {
  	content: '';
  	width: 100%;
  	height: 1px;
  	background-color: $light-peach;
  	position: absolute;
  	bottom: 8px;
  	left: 0;
  	opacity: 1;
  	transition: all 200ms ease-in;
  }
}

.clothes-item__sign {
	margin-right: 3px;
	margin-left: 3px;
}

.clothes-item__price {
  font-family: $demi;
	text-transform: uppercase;
}

//---------------------------------------
.item-info__total-sum {
	margin-bottom: 55px;
  font-family: $demi;
  font-size: rem(30);

  &.total-sum--share {
  	margin-bottom: 37px;
  }
}

//---------------------------------------
.item-info__line {
  width: 100%;
  height: 2px;
  margin-bottom: 20px;
  background-color: $black;

  &.info__line-share {
  	margin-bottom: 11px;
  }
}


// ###  Нижний блок  ###
//---------------------------------------
.item-info__bottom-block {
	display: flex;
	flex-direction: column;

	position: absolute;
	bottom: 40px;
	left: 50%;
	width: 80%;
	transform: translateX(-50%);
}

.item-info__info-social-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.info-social__text {
  font-size: rem(16);
  font-family: $demi;
  line-height: 1.31;
}

.info-social__social-link {
	display: inline-block;
	text-decoration: none;
	font-size: 0;

	a {
		transition: all 200ms ease-in;
		&:hover {
			opacity: 0.6;
		}
	}

	.vk {
		margin-right: 7px;
	}
}


// ###  Поделись с подругами  ###
//---------------------------------------
.item-info__item-share {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.item-share__text {
	margin-bottom: 9px;
  font-family: $book;
  font-size: rem(13);
  line-height: 1.62;
  letter-spacing: 1.8px;
  text-align: center;
  color: $black;
  text-transform: uppercase;
}


//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {

	.lookpage-item {
		height: auto;
		flex-direction: column;
		padding-bottom: 22px;
	}

	
	//---------------------------------------
	.lookpage-item__mobile-title {
		display: block;
	}
	
	//---------------------------------------
	.lookpage-item__item-pic {
		width: auto;
		height: auto;
		position: relative;
		text-align: center;

		img {
			position: relative;
			top: auto;
			right: auto;
			left: auto;
			bottom: auto;
			margin: none;
			max-width: auto;
			max-height: auto;
			height: auto;
			width: 100%;
		}

		&.item-pic--right {
			order: 0;
		}

	}

	// ###  Информация  ###
	//---------------------------------------
	.lookpage-item__item-info {
		display: block;
		width: 100%;
		// padding-top: 22px;
		padding-top: 0;
		padding-left: 15px;
		padding-right: 15px;
		padding-bottom: 0px;
		background-color: #fff;
		border: none;

		&.item-infom--border-top {
			border-top: none;	
		}

	}

	.item-info__item-number-wrap,
	.item-info__title,
	.item-info__bottom-block,
	.item-info__line,
	.item-info__icon,
	.item-info__item-share {
		display: none;
	}


	//---------------------------------------
	.item-info__clothes-list {
		margin-bottom: 11px;
		padding-bottom: 13px;
		border-bottom: 3px solid $black;

		li:last-child {
			border: none;
		}
	}

	.clothes-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid rgba(38,34,34,0.1);
	}

	.clothes-item__name {
	  font-size: rem(18);
		line-height: 2.67;
	  &:after {
	  	display: none;
	  }
	}

	.clothes-item__sign {
		display: none;
	}

	.clothes-item__price {
		font-size: rem(16);
		line-height: 3;
	}

	//---------------------------------------
	.item-info__total-sum {
		margin-bottom: 0;
		text-align: right;

		 &.total-sum--share {
  		margin-bottom: 0px;
  	}
	}
}


// ###  Vertical  ###
//---------------------------------------
@include breakpoint($height800) {

	.item-info__icon {
		top: 13%;
	}

}