/* 
	-----------------------------------------------
		BUTTONS
	-----------------------------------------------
*/

.btn {
	display: flex;
  width: 260px;
  height: 60px;
  border-radius: 3px;
  background-color: $black;
  text-align: center;
  text-decoration: none;
  transition: all 200ms ease-in;
  font-family: $demi;


  &:hover {
    background-color: $light-peach;

    .btn__text {
      color: $black;
    }

    .icon {
      svg {
        fill: $black;
      }
    }
  }

  &.btn-share {
    width: 300px;
    justify-content: center;
    align-items: center;

    .btn__text {
      margin: 0;
    }
  }

  .icon {
    display: block;
    margin-right: 10px;
    position: relative;
    top: 2px;
    svg {
      width: 16px;
      height: 12px;
      fill: #fff;
      transition: all 200ms ease-in;
    }
  }
  .icon-share {
    display: block;
    svg {
      position: relative;
      top: -4px;
    }
  }
}

.btn__text {
	margin: auto;
  font-size: rem(13);
  line-height: 2.3;
  letter-spacing: 4.1px;
  text-align: center;
  color: $white;
  text-transform: uppercase;
  transition: all 200ms ease-in;
  position: relative;
  top: 1px;
}

//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {
  
  .btn {
    width: 100%;

    &.btn-share {
      width: 100%;
    }
  }
}