/* 
	-----------------------------------------------
		SOCIAL LINE
	-----------------------------------------------
*/

.social-line {
	padding-top: 23px;
	padding-bottom: 25px;
}


//---------------------------------------
.social-line__text-wrap {
  font-size: rem(30);
  line-height: 1.5;
  text-align: center;

  &.text-wrap--share {
	  display: flex;
	  justify-content: center;
	  align-items: center;
	}
}

.social-line__text {
	position: relative;
	top: 1px;
	&.text-share {
		margin-right: 18px;
	}
}


//---------------------------------------
.social-line__btn {
	display: inline-block;
  width: 155px;
  height: 42px;
	margin: 0 4px;

  border-radius: 100px;
  background-color: $black;
	font-family: $demi;
  font-size: rem(24);
  color: $white;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  transition: all 200ms ease-in;

  div {
  	position: relative;
  	top: 3px;
  }

  &.fb {
  	&:hover {
  		background-color: #5a7cbc;
  	}
  }

  &.vk {
  	&:hover {
  		background-color: #678db9;
  	}
  }

	.fb-icon,
	.vk-icon {
		display: none;
	}

	.fb-icon {
		margin-right: 8px;
		position: relative;
		top: 4px;
		svg {
			  width: 15px;
			  height: 14px;
		}
	}

	.vk-icon {
		margin-right: 8px;
		position: relative;
		top: 7px;
		svg {
	    width: 19px;
		  height: 17px;
		}
	}

}


//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {

	.social-line {
		padding-top: 22px;
		padding-bottom: 30px;
		padding-left: 10px;
		padding-right: 10px;
	}
	
	//---------------------------------------
	.social-line__text-wrap {
	  font-size: rem(24);
	  line-height: 1.13;

	  .hide-mobile {
	  	display: none;
	  }

	  &.text-wrap--share {
		  flex-direction: column;
		}

	}

	.social-line__text {
		display: block;
		margin-bottom: 27px;
		.text-bold {
			font-family: $demi;
		}
	}

	.social-line__text-mobile-share {
		display: block;
	}


	//---------------------------------------
	.social-line__btn  {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 60px;
	  margin: 0 auto 6px;
	  font-size: rem(13);
	  line-height: 2.3;
	  letter-spacing: 4.1px;
	  border-radius: 3px;
	  text-transform: uppercase;

	  .fb-icon,
		.vk-icon {
			display: inline-block;
		}
	}

	
}
