/* 
	-----------------------------------------------
		STYLE TIP
	-----------------------------------------------
*/

.style-tip {
	min-height: 400px;
	height: 50vh;
	position: relative;
}


//---------------------------------
.style-tip__digit {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 5;
	width: 100%;
	height: 90%;
	background-position: center;
	background-repeat: no-repeat;

	&.style-tip__one {
		background-image: url(../img/svg/1.svg);
		background-size: contain;
	}
	&.style-tip__two {
		background-image: url(../img/svg/2.svg);
		background-size: contain;
	}
	&.style-tip__three {
		background-image: url(../img/svg/3.svg);
		background-size: contain;
	}
}


//---------------------------------
.style-tip__title {
	height: 33%;
	position: relative;
	z-index: 10;
  font-family: $demi;
  font-size: rem(12);
  line-height: 1.75;
  letter-spacing: 4px;
  text-align: center;
  text-transform: uppercase;

  display: flex;
  align-items: center;
  justify-content: center;

  span {
  	position: relative;
  	top: 10px;
  }
	
}

.style-tip__content-wrap {
	height: 67%;
	position: relative;
	z-index: 10;
}

.style-tip__descr {
	width: 700px;
	margin: 0 auto;
  font-size: rem(36);
	text-align: center;
  line-height: 1.25;
  text-align: center;
  color: $black;

  &.descr--small {
  	width: 680px;
  }
}


//---------------------------------
.style-tip__quotes {
	display: none;
	margin-bottom: 21px;
	text-align: center;
	position: relative;
	z-index: 5;
	svg {
		width: 35px;
	  height: 21px;
	}

	&.quotes-rotate {
		margin-top: 33px;
		transform: rotate(-180deg);
	}
}



//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {
	.style-tip {
		min-height: 110vh;
		// padding-top: 55px;
		// padding-bottom: 50px;
		padding-right: 18px;
		padding-left: 18px;
	}


	.style-tip__digit {
		&.style-tip__two,
		&.style-tip__three {
			height: 100%;
		}

		&.style-tip__two {
			background-size: 137%;
		}

		&.style-tip__three {
			background-size: 119%;
		}
	}


	//---------------------------------
	.style-tip__title {
		height: 27%;
	}


	//---------------------------------------
	.style-tip__content-wrap {
		// position: absolute;
		// top: 57%;
		// left: 50%;
		// transform: translate(-50%, -50%);
		// margin-bottom: 50px;
	}


	.style-tip__descr {
		max-width: 280px;
		margin: 0 auto 33px;
		position: static;
		top: auto;
		left: auto;
		transform: translate(0%, 0%);

		text-align: center;
	  font-size: rem(24);
	  line-height: 1.5;
	  text-align: center;
	  color: $black;
	}
	
	//---------------------------------------
	.style-tip__quotes { 
		display: block;

		&.quotes-rotate {
			// margin-top: 0;
			// margin-bottom: 10%;
		}
	}

}