/* 
	-----------------------------------------------
		FOOTER LOOGKPAGE
	-----------------------------------------------
*/

.footer-lookpage {
	padding-top: 24px;
	padding-bottom: 21px;
	padding-left: 29px;
	padding-right: 19px;
	background-color: $light-grey;
}



.footer-lookpage-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
}


// ###  Ссылка на сайт  ###
//---------------------------------------
.footer-lookpage__footer-link {
	position: relative;
	top: 1px;
  font-size: rem(16);
}

.footer-link__line {
	display: inline-block;
	font-family: $book;
	margin: 0px 7px;
}

.footer-link  {
	display: inline-block;
	text-decoration: none;
	color: $black;
	transition: opacity 0.5s;
	
	&:hover {
		opacity: 0.5;
	}
}


// ###  Социальные кнопки  ###
//---------------------------------------
.footer-lookpage__footer-social {
	display: flex;
	align-items: center;
}

.footer-social__text {
	display: inline-block;
	margin-right: 10px;
	font-size: rem(16);

	position: relative;
	top: 1px;
}

.footer-social__link {
	font-size: 0;

	.vk,
	.fb {
		margin-right: 8px;
	}

	.vk,
	.fb,
	.inst {
		svg {
			fill: $black;
			transition: all 200ms ease-in;
		}
	}

	.vk {
		&:hover {
			svg {
				fill: #678DB9;
			}
		}
	}

	.fb {
		&:hover {
			svg {
				fill: #5A7CBC;
			}
		}
	}

	.inst {
		&:hover {
			svg {
				fill: #B99070;
			}
		}
	}
}



//---------------------------------------
// ###  Media Queries  ###
//---------------------------------------
@include breakpoint($mobile) {

	.footer-lookpage {
		padding-top: 15px;
		padding-bottom: 15px;
		padding-left: 0;
		padding-right: 0;
	}
		
	.footer-lookpage-wrap {
		flex-direction: column;
		padding-left: 0;
		padding-right: 0;
	}
	
	//---------------------------------------
	.footer-lookpage__footer-link {
		order: 1;
	  font-size: rem(16);
	}

	//---------------------------------------
	.footer-lookpage__footer-social {
		width: 100%;
		display: flex;
		align-items: center;
		flex-direction: column;
		margin-bottom: 11px;
		padding-bottom: 18px;
		border-bottom: 1px solid rgba($black, 0.12);
		
	}

	.footer-social__text {
		margin-bottom: 13px;
	}
		
}